    summaryMarkup: `
    <p> Coming off of the cusp of Illinois's first Nationals appearance in six years, expectations were at an all-time high for the 2022 squad. Under the leadership of seniors Tyler Smiles and Joseph Kennedy, and junior Patrick Driscoll, Rise had huge holes to fill after losing more than half the team to graduation due to Covid-19 eligibility rules. Sophomore Eli Artemakis continued making defining his legacy at Illinois after taking home the 2021 National Rookie of the Year. Freshman Kai Bowen, with the poise of a seasoned veteran after playing the college series in the fall, was ready for more of the offensive load in the upcoming season. Coaches Eric Badger and Brad Bolliger returned to steer the team towards another Nationals run.</p>
    <p> Bid allocation, similar to the season prior, was determined by USAU regional and historical data rather than by regular season performance. This did not deter the team from finding the highest competition possible by submitting bids to Florida Warm-Up, Missouri Loves Company, and Easterns. Unfortunately, a record-breaking snow storm inhibited all forms of travel to Tampa Bay the weekend of Rise's kick-off tournament. </p>
    <p> With the internal cancellation of Warm-Up, the wait for Missouri Loves Company felt like an eternity. When the time to travel to Columbia, Missouri did come, the team was anxious to compete. A universe-point loss to a highly-ranked Minnesota team along with a convincing victory over Colorado State highlighted pool play. A prequarters loss to Missouri spelled the end for any hopes of winning the tournament. From that point forward, the coaching staff directed all consolation games to be growth opportunities for the roster's newcomers. </p>
    <p> The Easterns tournament lineup heavily reflected the makeup of teams from Nationals the season prior. The competition was stiff, even in pool play. Illinois played Georgia and California close but struggled to convert on defense, losing both matchups. A consolation victory over Texas marked Rises biggest win of the season to that point. Illinois proved it could hold its own against the nation's best teams and was ready for the postseason.</p>
    <p> Illinois had not won Sectionals since 2014. This season changed that. Rantoul's weather threw everything it had at us, but even thirty-mile-per-hour winds couldnt stop Rise from going undefeated in pool play with a point differential of eighteen over two games. Winning the pool sent Rise directly to the conference semifinal to face the University of Chicago in a revenge game from last season's championship. Rise advanced to the finals with a 10-8 victory. In the battle for Chicagos Big Ten school, Northwestern and Illinois traded points the entire game, forcing universe point with a conference title on the line. In a back and forth final point, Sophomore Eli Artemakis layout blocked Northwestern on two separate occasions and threw the game-winning huck to senior and Chicago Machine teammate Joseph Kennedy. For the first time in eight years, Illinois hoisted the conference title.</p>
    <p> Illinois easily handled Grand Valley State and Northwestern during Great Lakes Regionals pool play. The offensive line was rolling and the defense converted breaks early which created cushions in each of the games. The pair of victories earned Rise a berth in the regional quarterfinal against Notre Dame. Consistent with Saturdays performance, Rise broke early and built up a sizable lead at the half. Concerned with load management for the next round, Illinois squandered its late three-point lead and failed to hold on universe point. With all hopes of repeating at Nationals dashed, emotions were high. That being said, the team never quit and closed out Sunday with two victories against familiar foes in Chicago and a thrice-beaten Northwestern. Illinois placed fifth at Great Lakes Regionals. </p>
    <p> The college careers of Callahan-nominee Joseph Kennedy, Tyler Smiles, Mike Musial, and John Wolff had come to a close. While Rise did not accomplish its season goal of becoming a perennial Nationals team, this group of young men grew so much throughout the process. The implementation of throwing mentors created bonds on and off the field. Team culture improved. Skills sharpened and work ethics hardened. Those returning in the fall committed to playing club all over the country, and the will to win was stronger than ever. </p>
    <p> Eli Artemakis earned a spot on the All-Region First Team. </p>
    `,
