    summaryMarkup: `
        <p>During the Summer of 2020, Illinois Ultimate went through a complete rebrand. Team leadership, led by Reid Martin, wanted a name, image, and likeness that reflected the cultural rebirth of the program. Born was Illinois Rise. </p>
        <p>With a new name and purpose, Illinois Rise emerged from the ashes as a program ready to return to the national stage. The Class of 2019s large senior class left many holes in what was a very deep roster the year prior. Seniors Drake Hill, Lucas Gough, and Ben Lee were entering their senior years. Freshmen Patrick Driscoll, Kevin Zhou, Jack Joshi, and Andy Kennedy (commonly known as The Freshman Four) joined an Illinois team primed for new talent in a new era. Reid Martin, Anthony Ruzzo, and Joseph Kennedy led the team as its captains. Daniel McDonald and Eric Badger returned as coaches. </p>
        <p>Rise traveled down south to Tuscaloosa, Alabama to attend T-Town Throwdown for the season-opening tournament. Seeded third out of fourteen, Illinois swiftly defeated Mississippi and South Florida in pool play, each by a score of 11-2. Alabama-Huntsville was the only team that stood in the way of taking the pool. Led by Atlanta Hustle and Atlanta Chain Lightning defensive phenom Brett Hulsmeyer, the Nightmares put up a real fight but eventually lost on universe by a score of 11-10. The next day brought a quarterfinals matchup against nationally-ranked LSU, one of the tournaments top contenders. The preseason hype surrounding the Purple Haze wasnt enough to compete with the likes of a steamrolling Rise. Illinois advanced to the semifinals to face Georgia Tech after beating LSU 15-12. The team was building momentum the deeper it advanced into the bracket. Illinois handled Georgia Tech 14-11 to advance to the finals to face host school Alabama. Yellow Hammer was the toughest competition yet, but the tandem of Joseph Kenndy and Drake Hill was too much for any defense to this point in the tournament. Alabama threw different zone looks the entire game but nothing could stop the Illinois offense that Sunday in Tuscaloosa. Rise entered the national rankings at #24 the following week. </p>
        <p>Presidents Day Invite in La Jolla, California was the next stop on Illinoiss journey back to Nationals. Expectations were high coming off of the T-Town victory the month before. Rise notched an early victory against UC-Davis but dropped the next two against Oklahoma State and Cal Poly-SLO team led by Jake Thorne and D-1 Mens Rookie of the Year Calvin Brown. Sunday proceeded to mirror Saturday. Illinois won its opening game against San Diego State but dropped the next two against Oregon State and UC-San Diego. Consolation brought another pair of losses against USC and UC-Davis. The teams defense struggled all weekend to contain deep shots that werent an issue in Tuscaloosa. The team needed major schematic retooling before Centex in March. </p>
        <p>March of 2020 had other plans for the world. Unfortunately, all hope of finishing the season was lost due to the COVID-19 pandemic. All anyone could do was wait and prepare for the eventual return of ultimate.</p>
        `,
